@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "../theme/theme";

.smb-steps {
  margin-top: 40px;
  text-align: center;

  @include mixins.media("tablet-l") {
    margin-top: 60px;
  }

  &__margin {
    margin-bottom: 24px;
  }

  h1,
  h2,
  h3 {
    font-family: variables.$font-family--title;
    font-weight: 400;
    text-align: center;
    color: variables.$color-principal;
  }

  h1 {
    margin-bottom: 12px;
    font-weight: 900;
    @extend .h2;

    @include mixins.media("tablet-l") {
      margin-bottom: 32px;
    }
  }

  h2 {
    margin-bottom: 20px;
    @extend .h4;
  }
}

.smb-steps__step-content {
  @include mixins.boxFlex(initial, initial, column);

  h3, .h3 {
    max-width: 278px;
    margin-bottom: 4px;
    margin-left: auto;
    margin-right: auto;
    @extend .h5;

    @include mixins.media("tablet-l") {
      margin-bottom: 10px;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      display: -webkit-box;
      overflow: hidden;
    }
  }

  p {
    margin: 0 auto;
    max-width: 344px;
    font-size: variables.$font-size--general-m;
    text-align: center;
    line-height: variables.$line-height--general-m;
    color: variables.$color-black;
  }

  .smb-steps__step-number {
    height: 39px;
    margin: 0 auto 12px;
    width: 39px;

    @include mixins.media("tablet-l") {
      display: none;
    }
  }
}

.smb-steps__step-number-wrapper {
  margin-bottom: 12px;
  @include mixins.boxFlex(space-between);

  @include mixins.media("tablet-l") {
    margin: 0 auto 12px;
  }

  @include mixins.mediaCustom("max-width: 991px") {
    display: none;
  }

  &.step-1 {
    @include mixins.media("tablet-l") {
      justify-content: center;
    }
  }

  &.step-2 {
    @include mixins.media("tablet-l") {
      width: 53%;
    }

    .smb-steps__step-number + span {
      width: 70%;
    }
  }

  &.step-3 {
    @include mixins.media("tablet-l") {
      width: 73%;
    }
  }
}

.smb-steps__step-number {
  margin: 0;
  padding: 5px 15px;
  border-radius: 100%;
  background-color: variables.$color-accent-3;
  font-family: variables.$font-family--title;
  font-size: variables.$font-size--mobile-h6;
  font-weight: 900;
  line-height: variables.$line-height--mobile-h6;
  color: variables.$color-white;
  @include mixins.boxFlex;

  @include mixins.media('tablet-l') {
    font-size: variables.$font-size--desk-h6;
    line-height: variables.$line-height--desk-h6;
  }

  & + span {
    display: block;
    height: 1px;
    width: 40%;
    background-color: variables.$color-principal;
  }
}

.smb-steps__step-content-wrapper {
  .swiper-wrapper {
    max-width: 80vw;

    @include mixins.media("tablet-l") {
      max-width: initial;
    }
  }

  &.step-1 .swiper-wrapper {
    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(repeat(1, 1fr), unset, 24px, 0);
    }
  }
  &.step-2 .swiper-wrapper {
    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(repeat(2, 1fr), unset, 24px, 0);
    }
  }
  &.step-3 .swiper-wrapper {
    @include mixins.media("tablet-l") {
      @include mixins.boxGrid(repeat(3, 1fr), unset, 24px, 0);
    }
  }

  .swiper-pagination {
    margin-top: 12px;
  }
}
