@use "../abstracts/variables";
@use "../abstracts/mixins";

%button {
  position: relative;
  padding: 10px;
  font-weight: 300;
  border-style: none;
  border-radius: 0px;
  cursor: pointer;
  transition: all 0.5s ease;
  text-transform: uppercase;
  white-space: nowrap;
  text-align: center;
  @include mixins.boxFlex;
  @include mixins.clickEffect;

  &.btn-link {
    display: block;
    width: fit-content;
    text-align: center;

    svg:not(.animation) {
      vertical-align: middle;
    }

    span {
      vertical-align: middle;
    }
  }

  svg {
    height: 20px;
    width: 20px;
  }

  span + svg {
    margin-left: 7.25px;
  }

  svg.icon-plus {
    margin-right: 7.25px;
  }

  &.disabled,
  &:disabled {
    cursor: initial;
    transform: unset;
  }

  &:hover {
    @include mixins.btn-type(var(--color-btn-color-hover), var(--color-btn-bg-hover), var(--color-btn-border-hover));
  }
}

.link-vanilla {
  @include mixins.clickEffect;
  display: inline-flex;
  padding: 0;
  color: variables.$color-link;
  text-transform: uppercase;
  text-decoration: none;

  &:hover {
    text-decoration: none;
  }

  span {
    margin: 0 8px;
    @include mixins.linkEffect(currentColor, 1px);
  }

  svg {
    height: 20px;
    width: 20px;
  }

  &.disabled,
  &:disabled {
    cursor: initial;
    color: variables.$color-accent-2;
  }

  &.btn-s,
  &.btn-m,
  &.btn-l {
    padding: 0;
  }
}

.btn {
  &-1 {
    @extend %button;
    @include mixins.btn-type(var(--color-btn-color-1), var(--color-btn-bg-1), var(--color-btn-border-1));

    &.disabled,
    &:disabled {
      opacity: 0.5;
    }

    span {
      margin: 0 6px;
    }
  }

  &-2 {
    @extend %button;
    @include mixins.btn-type(var(--color-btn-color-2), var(--color-btn-bg-2), var(--color-btn-border-2));

    &.disabled,
    &:disabled {
      @include mixins.btn-type(var(--color-base-bg-2), transparent, var(--color-base-bg-2));
    }

    span {
      margin: 0 6px;
    }

    #loading,
    .loading {
      border-top-color: variables.$color-principal;
    }
  }

  &-s {
    padding: 10px;
    font-size: variables.$font-size--general-up-s;
  }

  &-m {
    padding: 10px 43px;
    font-size: variables.$font-size--general-up-m;

    svg {
      height: 20px;
      width: 20px;
    }
  }

  &-l {
    padding: 14px 20px;
    font-size: variables.$font-size--general-up-l;

    span {
      margin: 0 8px;
    }
  }

  &__w-100 {
    width: 100%;
  }

  &__p-50 {
    padding-right: 50px;
    padding-left: 50px;
  }

  &__p-70 {
    padding-right: 70px;
    padding-left: 70px;
  }
}

.buttons__social {
  flex-direction: row;
  * {
    border: none;
  }
  div {
    box-sizing: content-box;
  }

  &--media {
    display: flex;
    align-items: center;
    padding: 8px;
    border: 1px solid #dadce0;
    margin: 5px;
    border-radius: 50%;
    background: none;
    cursor: pointer;
    &:hover {
      box-shadow: none;
      border-color: #d2e3fc;
      outline: none;
    }
    svg {
      width: 23px;
      height: 23px;
    }
  }
}
