@use "../abstracts/variables";
@use "../abstracts/mixins";
@use "../theme/theme";

.sale-account {
  height: 100%;
  width: 100%;

  & > div,
  & > div > *:not(.sale-account__title, .sale-account__btn-retour),
  .card-body,
  .card-body > div {
    height: 100%;
  }

  .smb__container .v-step:not(:last-of-type) {
    margin-bottom: 60px;
  }

  .form__textfield-label input,
  .form__textfield-label textarea,
  .form__textfield-label select {
    text-align-last: center;
  }

  .open-popup-link__wrapper {
    margin-bottom: 24px;
    margin-top: 24px;
    text-align: left;
  }

  .open-popup-link {
    @include mixins.linkEffect(currentColor, 1px);
  }

  .smb-account__second-form {
    display: flex;
    margin-top: 8px;

    a {
      white-space: normal;
    }
  }

  .smb-account__second-form-container {
    @include mixins.boxFlex(initial, center)
  }

  .smb-account__second-form-sep {
    margin: 8px 8px 0 8px;
  }
}

.sale-account__item {
  border: 1px solid variables.$color-principal;

  &:not(:last-child) {
    margin-bottom: 24px;
  }

  &--empty {
    height: 100%;
  }
}

.sale-account__item-info {
  align-items: center;
  padding: 16px;
  @include mixins.boxGrid(60px 1fr, auto, max(16px));

  @include mixins.media('tablet-l') {
    column-gap: max(20px);
  }

  @include mixins.media('desktop-m') {
    column-gap: max(55px);
  }

  a.btn-1 {
    transform: unset;
    cursor: initial;

    &:hover {
      color: variables.$color-white;
      background-color: variables.$color-principal;
    }
  }
}

.sale-account__item-image {
  img {
    width: 60px;
    height: 60px;
  }
}

.sale-account__item-order-num {
  @include mixins.boxFlex(space-between);

  @include mixins.media('tablet-l') {
    @include mixins.boxGrid(repeat(2, auto), auto, max(30px));
  }

  @include mixins.media('desktop-m') {
    column-gap: max(55px);
  }
}

.sale-account__item-text {
  @include mixins.media('tablet-l') {
    align-items: center;
    @include mixins.boxGrid(repeat(3, auto), auto, max(20px));
  }

  @include mixins.media('desktop-m') {
    column-gap: max(55px);
  }
}

.sale-account__item-status {
  max-width: 163px;
  min-width: 163px;
  @include mixins.boxFlex(initial, initial);

  @include mixins.mediaCustom('max-width: 991px') {
    max-width: initial;
    margin: 8px 0;
  }

  a {
    white-space: break-spaces;
    width: 100%;
    min-width: 186px;

    @include mixins.mediaCustom('max-width: 991px') {
      width: auto;
    }
  }
}

.sale-account__item-btn-toggle a {
  @include mixins.boxFlex(flex-start);
  color: variables.$color-black;

  @include mixins.media('tablet-l') {
    justify-content: center;
  }

  svg {
    display: block;
    height: 10px;
    margin-left: 14px;
    width: 10px;

    path {
      fill: variables.$color-principal;
    }
  }
}

.sale-account__item-detail-accordion {

  & > li {
    border-top: 1px solid variables.$color-principal;
  }

  &,
  * {
    list-style: none;
  }
}

.sale-account__item-detail-content,
.sale-account__item-detail-title {
  padding: 16px;
}

.sale-account__item-detail-title {
  text-transform: uppercase;
  color: variables.$color-principal;
  @include mixins.boxFlex(space-between);

  svg {
    display: block;
    height: 10px;
    margin-left: 14px;
    width: 10px;
  }
}

.sale-account__item-detail-content {
  padding-top: 0;

  .product-summary__content {
    @include mixins.boxGrid(1fr, auto, 0, 24px);

    @include mixins.media('tablet-l') {
      align-items: center;
      @include mixins.boxGrid(repeat(3, 1fr), auto, 16px, 0);
    }

    ul:nth-of-type(2) {
      align-self: flex-start;
    }

    li:not(:last-child) {
      margin-bottom: 8px;
    }
  }

  .product-summary__img-container {
    @include mixins.media('tablet-l') {
      justify-self: flex-end;
    }

    button.btn-1 {
      width: 100%;
      margin-bottom: 16px;
    }
  }

  .product-summary__img {
    ul {
      @include mixins.boxGrid(1fr 1fr, auto, 16px, 0);
    }

    li {
      position: relative;
    }

    li:first-child {
      margin-bottom: 0;
    }

    img {
      height: 115px;
      width: 115px;
    }

    .remove-btn {
      position: absolute;
      right: 6px;
      top: 6px;
      @include mixins.clickEffect;
    }
  }
}

.sale-account__item-detail-content.discussion {
  textarea {
    border-color: variables.$color-principal;
    height: 125px;
    margin-bottom: 16px;
    max-width: 342px;
    width: 100%;
  }

  .btn-1 {
    min-width: 118px;
  }

  .sale-account__input-file  {
    margin-bottom: 16px;
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-s;

    .upload-img {
      display: none;
    }
  }

  .input-file-upload {
    position: relative;
    margin-bottom: 16px;
    max-width: 342px;


    .upload-label {
      position: absolute;
      padding: 8px;
      background-color: variables.$color-secondary;
      color: variables.$color-accent-3;
      text-transform: uppercase;
      @include mixins.clickEffect;
    }

    input {
      padding: 8px;
      width: 100%;
      opacity: 0;
    }
  }
}

.discussion__comment {
  max-width: 376px;
  margin-bottom: 50px;
  padding: 10px;
  border-radius: 10px;
  width: 100%;

  @include mixins.media('tablet-l') {
    width: 50%;
  }

  &.staff {
    margin-left: auto;
    background-color: #003bc33d;
  }

  &.client {
    background-color: rgb(247 241 236 / 50%);
  }
}

.discussion__comment-head {
  margin-bottom: 24px;
  @include mixins.boxFlex(space-between);

  h4,
  h3 {
    margin-bottom: 0;
    font-family: variables.$font-family--general;
    font-size: variables.$font-size--general-m;
    line-height: variables.$line-height--general-m;
  }
}

.discussion__comment-date {
  font-size: variables.$font-size--general-s;
  line-height: variables.$line-height--general-s;
}

.smb__form-check-container .form__checkbox {
  text-align: left;
}

.sale-account__btn-retour {
  margin-bottom: 24px;
  @include mixins.boxFlex(flex-end);

  .btn-1 {
    min-width: 109px;
  }
}

.sale-account__btn-submit {
  min-width: 157px;
  margin-top: 16px;
}



