@use '../abstracts/variables';
@use '../abstracts/mixins';
@use '../components/buttons';

#smb-navigation {
  position: absolute;
  top: -200px;
}

.smb {
  position: relative;
  margin-left: auto;
  margin-right: auto;


  .wizard-progress-with-circle,
  .wizard-nav.wizard-nav-pills,
  .wizard-header {
    display: none;
  }

  .vue-form-wizard,
  .vue-form-wizard .wizard-tab-content {
    padding: 0;
  }

  .auth__login {
    margin-top: 0;
  }

  .wizard-footer-right .wizard-footer-right.wizard-btn {
    @extend .btn-1;
  }

  .wizard-footer-left .wizard-btn {
    @extend .btn-2;
  }

  .wizard-card-footer.clearfix {
    margin-top: 16px;
    padding: 0;
  }

  .sell-form-head h2 {
    font-weight: 400;
    @extend .h2;
    margin-bottom: 0;
  }

  .alert.alert-success {
    border: 1px solid variables.$color-principal;
    text-align: center;
    padding: 19px;

    @include mixins.media('tablet-l') {
      margin-left: - variables.$paddings-side-m;
      margin-right: - variables.$paddings-side-m;
      padding: 19px 94px;
    }

    a {
      @include mixins.linkEffect(currentColor, 1px)
    }
  }

  .form__checkbox label {
    width: 100%;
    text-align: left;
  }

  .smb__buttons.two {
    @include mixins.boxFlex;

    & > div {
      float: unset;
    }

    & > div:first-child button {
      margin-right: 15px;
    }
  }
}

.smb__container {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-width: 436px;
  width: 100%;
  text-align: center;

  .input-error,
  .text-danger {
    margin-bottom: 4px;
    margin-top: 4px;
    font-size: variables.$font-size--general-up-s;
    line-height: variables.$line-height--general-up-s;
    color: variables.$color-error;
  }
}

.smb .form__textfield-label {
  input,
  textarea,
  select {
    width: 100%;
    text-align-last: center;

    &::placeholder {
      text-align: center;
    }
  }

  &:last-of-type {
    margin-bottom: 20px;
  }
}

.smb__form-step {
  margin-bottom: 16px;

  @include mixins.media('tablet-l') {
    margin: 0 auto;
    max-width: 640px;
  }

  & > h3, & > .h3 {
    font-family: variables.$font-family--general;
    font-size: variables.$font-size--general-up-l;
    font-weight: 300;
    line-height: variables.$line-heigh--general-l;
    text-transform: uppercase;
    text-align: center;
  }

  textarea {
    min-height: 76px;
  }

  .smb__form-photos {
    @include mixins.boxGrid(repeat(2, 1fr), repeat(3, 108px), 16px, 16px);
  }

  &.last-step {
    .form__textfield-label--spaced {
      margin-bottom: 16px;
    }
  }
}

.smb__form-photos-input {
  border: 1px solid variables.$color-background-4;
  @include mixins.clickEffect;

  .upload-btn-wrapper {
    position: relative;
    height: 100%;
  }

  .upload-btn-wrapper.uploaded {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;

    button {
      opacity: 0;
    }
  }

  .remove-btn {
    position: absolute;
    right: 10px;
    top: 10px;
  }

  input {
    height: 100%;
    width: 100%;
    opacity: 0;
  }

  button {
    bottom: 0;
    left: 0;
    position: absolute;
    right: 0;
    top: 0;
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-up-s;
    background-color: variables.$color-secondary;
    color: variables.$color-background-2;
  }

  span {
    text-transform: uppercase;
  }
}

.v-step-head h2,
.v-step-head .h2,
.smb__login-title {
  display: block;
  margin-bottom: 0;
  font-size: variables.$font-size--mobile-h5;
  font-weight: 400;
  font-family: variables.$font-family--title;
  line-height: variables.$line-height--mobile-h5;
  text-align: center;
  color: variables.$color-principal;
}

.v-step-head {
  margin-bottom: 22px;

  @include mixins.media('tablet-l') {
    margin-bottom: 30px;
  }

  h2,
  h3,
  .h2,
  .h3 {
    margin-bottom: 8px;
  }

  h3, .h3 {
    font-family: variables.$font-family--general;
    font-size: variables.$font-size--general-m;
    font-weight: 300;
    line-height: variables.$line-height--general-m;
    color: variables.$color-black;
  }

  p {
    font-family: variables.$font-family--title;
    color: variables.$color-principal;
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
    font-weight: 400;

    @include mixins.mediaCustom('max-width: 991px') {
      display: none;
    }
  }
}

.smb__form-check-container {
  text-align: left;
  margin-top: 20px;

  &:last-of-type {
    margin-bottom: 20px;
    margin-top: 24px;
  }

  a {
    @include mixins.linkEffect(currentColor, 1px);
  }

  .smb__form-check-title {
    display: block;
    margin-bottom: 4px;
    font-family: variables.$font-family--title;
    font-size: variables.$font-size--general-l;
    line-height: variables.$line-height--general-l;
    color: variables.$color-principal;
  }

  & > p {
    text-align: center;
    margin-bottom: 20px;
  }

  .form__checkbox {
    margin-bottom: 8px;

    label {
      width: 100%;
      font-size: variables.$font-size--general-m;
      line-height: variables.$line-height--general-m;
    }
  }
}

/*
  Pop up conditions
*/

.overlay {
  position: fixed;
  z-index: 995;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.527);
  opacity: 0.8;
  width: 100%;
  height: 100%;
}

#test-popup {
  position: absolute;
  transition: all .4s;
  left: 50%;
  transform: translate(-50%, -100%);

  &[data-active=false] {
    z-index: -1;
    opacity: 0;
    top: 25%;
  }

  &[data-active=true] {
    position: fixed;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    z-index: 1000;

    @include mixins.media('tablet-l') {
      transform: translate(-50%, -50%);
    }
  }

  .close-popup-link {
    position: absolute;
    right: 20px;
    top: 10px;
    font-weight: 600;
    font-size: 18px;
    color: gray;
    cursor: pointer;
  }
}

.smb-popup {
  .smb-popup__img-mobile {
    max-width: 316px;

    @include mixins.media('tablet-l') {
      display: none;
    }
  }

  .smb-popup__img-desktop {
    display: none;
    max-width: 850px;
    max-height: 440px;

    @include mixins.media('tablet-l') {
      display: block;
    }
  }

  img {
    width: 90vw;
  }
}

.description-modal {
  display: none;
  position: fixed;
  z-index: 20;
  top: 50%;
  left: 50%;
  height: 90%;
  max-height: 90%;
  width: 96%;
  padding: 14px 16px;
  overflow-y: auto;
  transform: translate(-50%, -50%);
  background-color: variables.$color-white;
  color: variables.$color-background-2;
  font-weight: 400;
  transition: all 0.4s ease-in;

  @include mixins.mediaCustom("min-width: 620px") {
    max-width: 582px;
  }

  @include mixins.media("tablet") {
    height: auto;
    padding: 20px 33px 33px 33px;
    width: 90%;
  }

  &[data-active="true"] {
    display: block;
  }

  .description-modal__close {
    display: block;
    margin-left: auto;
    line-height: 0;
    @include mixins.clickEffect;

    path {
      fill: variables.$color-black;
    }
  }

  .description-modal__title {
    display: block;
    margin-bottom: 5px;
    font-family: variables.$font-family--title;
    font-size: variables.$font-size--mobile-h4;
    line-height: variables.$line-height--mobile-h4;
    color: variables.$color-principal;
    text-align: center;
    text-transform: uppercase;
  }

  .description-modal__description {
    margin-bottom: 17px;
    padding: 12px;
    font-size: variables.$font-size--general-s;
    line-height: variables.$line-height--general-up-s;
    background-color: variables.$color-secondary;
    @include mixins.boxFlex(initial);

    @include mixins.media("tablet") {
      padding: 12px 31px;
    }

    &-icon {
      height: 36px;
      width: 36px;
    }

    &-text {
      font-weight: 300;
      color: variables.$color-black;
      margin-left: variables.$margin-xs;

      @include mixins.media("tablet") {
        margin-left: variables.$margin-m;
      }
    }
  }

  .description-modal__grid {
    @include mixins.boxGrid(repeat(2, 1fr), auto, 12px, 12px);

    @include mixins.media("tablet") {
      column-gap: 15px;
      row-gap: 17px;
    }
  }

  .description-modal__grid-item {
    border: 1px solid variables.$color-principal;
    padding: 11px;

    @include mixins.media("tablet") {
      padding: 11px 20px 20px 20px;
    }

    & > span {
      display: block;
      margin-bottom: 3px;
      font-family: variables.$font-family--title;
      font-size: variables.$font-size--mobile-h6;
      line-height: variables.$line-height--mobile-h6;
      color: variables.$color-principal;
    }

    & > div {
      font-size: variables.$font-size--general-s;
      line-height: variables.$line-height--general-up-s;
      color: variables.$color-background-2;
    }
  }

  .description-modal__grid.modal-shoulder-strap {
    .description-modal__grid-item > span {
      text-align: center;
      margin-bottom: 8px;

      @include mixins.media("tablet") {
        margin-bottom: 16px;
      }
    }

    .description-modal__grid-item {
      padding: 6px;

      @include mixins.media("tablet") {
        padding: 4px;
      }
    }

    .description-modal__grid-item img {
      display: block;
      height: 100%;
      margin: 0 auto;
      max-height: 145px;
      max-width: 218px;
      width: 100%;
      object-fit: contain;
    }
  }
}
